@import url(https://fonts.googleapis.com/css?family=DM+Mono&display=swap);
@import url(https://fonts.googleapis.com/css?family=Inconsolata&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Cutive&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

body {
  background-color: #242124;
  color: #BBBBBB;
  font-family: 'DM Mono', sans-serif; }

p.terminal {
  margin: 0px;
  padding: 0px; }

p.import {
  text-indent: 4.7em;
  margin: 0px;
  padding: 0px; }

p.todo {
  text-indent: 2.7em;
  color: #808080; }

p.next {
  margin: 15px; }

.fullscreen {
  height: 100vh;
}

.jumbotron {
  height: 100vh;
  background-color: #242124; }

.main {
  height: 100vh; }

.editor {
  border: 1px solid #696969;
  border-radius: 10px;
  margin-top: 30px;
  margin-bottom: 30px;
  background-color: #1B1B1B;
  color: #BBBBBB;
  box-shadow: 5px 8px 18px #343434; }

.init {
  color: #F67BAA; }

.func {
  color: #20B2AA; }

.packs {
  text-decoration: none;
  -webkit-text-decoration-style: none;
          text-decoration-style: none;
  color: #90EE90; }

a.packs:active, a.packs:hover {
  color: #90EE90;
  text-decoration: underline; }

.nav-tabs {
  background-color: #242124 !important; }

a.nav-link {
  color: #20B2AA !important; }

a.active.nav-link {
  background-color: #1B1B1B !important;
  color: #90EE90 !important; }

a.contacts {
  color: #BBBBBB !important; }

.mailto {
  font-size: larger; }

.footer {
  font-size: smaller; }

.btn-tabs {
    background-color: transparent;
    /* border-color: transparent;*/
    border: none;
    outline: none;
    box-shadow: none;
}

.tab-modal .modal-content {
  background-color: #1B1B1B;
}

.fa-2x {
  margin-left: 3px;
  margin-right: 3px;;
}

.fa-navigate {
  color: #BBBBBB;
}

h2 {
  font-family: 'Cutive', monospace;
}

ul {
  list-style-type: circle;
  list-style-position: outside;
  list-style-image: none;
}

